      A: I am an out-of-network provider and thus do not bill directly to any insurance plans. Session fees
      are collected at the time of your appointment. Payment is accepted via check, cash, or Venmo.
    </p>
    <p class="question">Q: How can I get my insurance to reimburse the cost of my sessions?/p></p>
